// DRAWING GLOBALS
import {gS,
        livecanvas, lctx, canvas, ctx,
        affineset, updateSymmetry, updateStyle, drawKeyToOrderMap,
        commitOp,
        gVoronoi
       } from './main';
import _ from 'underscore';
import {add2, sub2, scalar2, normalize, l2norm, l2dist, reflectPoint} from './math_utils';

import {drawHitCircle,drawSpline, drawSpline_pts, drawHitLine} from './canvas_utils';
var THREE = require("three");

export class PathOp {
  constructor(symmState, ctxStyle, points) {
    this.ctxStyle = ctxStyle;
    this.points = points;
    this.tool = "path";
    this.symmState = symmState;
    this.voronoi = gVoronoi;
  }

  render(ctx) {

    _.assign(ctx, this.ctxStyle);
    updateSymmetry(this.symmState);
    const drawOrder = drawKeyToOrderMap[this.ctxStyle.drawOrder]; // optional separation of stroke / fill layers
    this.line_id = 1;  // HACK OF AN AMAZING ORDER

    let ptidx = 0;
    for(let drawSet of drawOrder){
      for (let af of affineset) {
        this.voronoi.setNewColor(this.line_id);
        ctx.beginPath();
        let Tpt = af.onVec(this.points[0]);
        ctx.moveTo(Tpt[0], Tpt[1]);

        let sendPts = [];
        let pts = [];

        let ind = (this.line_id * 97 + (ptidx + 1))%10000;
        for(var i = 0; i < this.points.length; i++){
          let tmp_pt = af.onVec(this.points[i]);
          //tmp_pt[0]+=2*this.rnd[ind+i];
          //tmp_pt[1]+=this.rnd[ind+i];
          sendPts.push(new THREE.Vector2(tmp_pt[0],tmp_pt[1]));
          pts.push(tmp_pt);
        }
        drawSpline_pts(ctx,sendPts);

        //if (gS.ctxStyle.practiceMode == 0){
        this.voronoi.renderCurve(this.line_id * 97 + (ptidx + 1), pts); // One hack after another (last semester vibes)
       // }
        ptidx+=this.points.length;

        this.line_id += 2;
      }
    }
  }
}

//State Labels
const _INIT_ = 0;
const _OFF_  = 1;
const _ON_   = 2;
const _MOVE_ = 3;

const EPS = 1.0e-6; // lineTo cutoff for proximity of control point to vertex

//TODO: hide control points for most vertices unless they're "parent" vertex
//      is selected
export class PathTool {
  constructor() {
    //this.ops = [];
    this.points = [];
    this.state = _INIT_;
    this.ctrlPoint = [];
    this.pointsSelected = [];
    this.pointSelected = 0;
    this.hitRadius =  6;
    this.sleep0 = 0;
    this.rnd0 = [];
    this.rnd = [];
    this.actions = [
      {name: "cancel", desc: "cancel path", icon: "icon-cross", key: "Escape"},
      {name: "commit", desc: "start new path", icon: "icon-checkmark", key: "Enter"},
      {name: "back",   desc: "undo last point", icon: "icon-minus", key: "Backspace"},
      {name: "closepath",   desc: "close path", icon: "icon-stroke", key: "KeyC"},
      {name: "smoothclose",   desc: "smooth close path", icon: "icon-radio-unchecked", key: "KeyS"}
    ];
    this.voronoi = gVoronoi;

    for(var i = 0; i < 10000; i++) {
      this.rnd0[i] = Math.random();
      this.rnd[i] = 0;
    }
  }

  generateRandomNumbers(){
    var div = gS.ctxStyle.maxRnd;
    for(var i = 0; i < 10000; i++) {
      this.rnd[i] = this.rnd0[i]*div;
    }
  }

  liverender() {
    lctx.clearRect(0, 0, canvas.width, canvas.height);
    this.generateRandomNumbers();
    if(this.points.length==0){return;}
    this.line_id = 1;  // HACK OF AN AMAZING ORDER
    const drawOrder = drawKeyToOrderMap[gS.ctxStyle.drawOrder]; // optional separation of stroke / fill layers

    let ptidx = 0;
    for(let drawSet of drawOrder){
      for (let af of affineset) {
        this.voronoi.setNewColor(this.line_id);
        lctx.beginPath();
        let Tpt = af.onVec(this.points[0]);
        lctx.moveTo(Tpt[0], Tpt[1]);

        let sendPts = [];
        let pts = [];
        let ind = (this.line_id * 97 + (ptidx + 1))%10000;
        for(var i = 0; i < this.points.length ; i++){
          let tmp_pt = af.onVec(this.points[i]);

          let rnd_ind = (ind+i)%10000;
          tmp_pt[0] -= 2 * this.rnd[rnd_ind];
          tmp_pt[1] -= 2*this.rnd[rnd_ind];

          sendPts.push(new THREE.Vector2(tmp_pt[0],tmp_pt[1]));
          pts.push(tmp_pt);
        }


        drawSpline_pts(lctx, sendPts);

        //if (gS.ctxStyle.practiceMode == 0){
          this.voronoi.renderCurve(this.line_id * 97 + (ptidx + 1), pts); // One hack after another (last semester vibes)
        //}
        ptidx+=this.points.length;

        this.line_id += 2;
      }
    }
    //let ind = (this.line_id * 97 + (0 + 1))%10000;
    for(var i = 0; i < this.points.length; i++){
      let pt0 = this.points[i];
      drawHitCircle(lctx, pt0[0], pt0[1], this.hitRadius);
    }

    //next few lines - varda
    // if (this.sleep0>2 && this.sleep0<50){
    //   var img = this.voronoi.renderer.domElement.toDataURL();
    //
    //   var a = document.createElement('a');
    //   a.href = img;
    //   var tmp = "frame" + this.sleep0;
    //   a.download = tmp.slice();
    //   document.body.appendChild(a);
    //   a.click();
    //
    // }
    // this.sleep0+=1;

  }

  enter(op) {
    if(op) {
        updateStyle(op.ctxStyle);
        updateSymmetry(op.symmState);
        this.points = op.points;
        this.pointsSelected = [];
        this.ctrlPoint = [];
        this.state = _OFF_;
        this.liverender();
    }
    else {
      this.points = [];
      this.pointsSelected = [];
      this.ctrlPoint = [];
      this.state = _INIT_;
    }
  }

  exit() {
    this.points = [];
    this.pointsSelected = [];
    this.ctrlPoint = [];
    this.state = _INIT_;
  }

  commit() {
    if(this.state==_INIT_){return;} //empty data cases
    if(this.points.length<4){return;}
    let ctxStyle = _.clone(gS.ctxStyle);
    let symmState = _.clone(gS.symmState);
    commitOp(new PathOp(symmState, ctxStyle, this.points));
    lctx.clearRect(0, 0, livecanvas.width, livecanvas.height);
    this.points = [];
    this.pointsSelected = [];
    this.ctrlPoint = [];
    this.state = _INIT_;
    this.voronoi.resetColor();
    this.voronoi.removeAllObjects();
  }

  cancel() {
    lctx.clearRect(0, 0, livecanvas.width, livecanvas.height);
    this.state = _INIT_;
    this.points = [];
    this.ctrlPoint = [];
  }

  closepath() {
    if(this.points.length < 4 || l2dist(this.points[0],this.points[this.points.length-1])<EPS){
      return;
    }
    if(this.ctrlPoint.length === 0) {
      this.state = _OFF_;
      this.points.push(this.points[this.points.length-1]);
      this.points.push(this.points[0]);
      this.points.push(this.points[0]);
    } else {
      this.state = _OFF_;
      this.points.push(this.ctrlPoint);
      this.points.push(this.points[0]);
      this.points.push(this.points[0]);
      this.ctrlPoint = []; //clear tmp control pt
    }
    this.liverender();
  }

  smoothclose(){
    if(this.points.length < 4 || l2dist(this.points[0],this.points[this.points.length-1])<EPS){
      return;
    }
    if(this.ctrlPoint.length === 0) {
      this.state = _OFF_;
      this.points.push(this.points[this.points.length-1]);
      let reflpt = reflectPoint(this.points[0], this.points[1]);
      this.points.push(reflpt);
      //this.points.push(this.points[0]);
      this.points.push(this.points[0]);
    } else {
      this.state = _OFF_;
      this.points.push(this.ctrlPoint);
      let reflpt = reflectPoint(this.points[0], this.points[1]);
      this.points.push(reflpt);
      this.points.push(this.points[0]);
      this.ctrlPoint = []; //clear tmp control pt
    }
    this.liverender();
  }

  mouseDown(e) {

    let rect = livecanvas.getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    this.state = _ON_;
    let onPoint = false;
    let i = 0;
    while(i<this.points.length-1){
      let currPt = this.points[i];
      if (l2dist(currPt,pt)<this.hitRadius){
        onPoint = true;
        this.state = _MOVE_;
        this.pointSelected = i;
        break;
      }
      i+=1;
    }
    if (!onPoint){
      this.points.push(pt);
      this.liverender();
    }

  }

  mouseMove(e) {
    let rect = livecanvas.getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    if (this.state == _MOVE_){
      this.points[this.pointSelected] = pt;
      this.liverender();
    }
  }

  mouseUp(e) {
    let rect = livecanvas.getBoundingClientRect();
    let pt = [e.clientX - rect.left, e.clientY - rect.top];
    if (this.state == _MOVE_){
      this.points[this.pointSelected] = pt;
      this.state = _ON_ ;
      this.liverender();
    }
  }

  keyDown(e) {
    if(e.target.type){return;} // don't interfere with input UI key-events
    for(let action of this.actions){
      if(action.key == e.code){
        this[action.name]();
      }
    }
  }

  back() {
    if(this.points.length > 1 &&
       this.state == _OFF_) {
         let [cpt0,cpt1,pt1] = this.points.splice(-3);
         this.ctrlPoint = cpt0;
         this.liverender();
    } else if (this.state == _OFF_) {
      this.points = [];
      this.state = _INIT_;
      this.liverender();
    }
  }

}



